import React from "react";
import { Link } from "gatsby";
import Layout from "../components/layout";
import CardBox from "../components/cardbox";
import style from "./category.module.scss";

interface CategoryPageProps {
  pageContext: {
    posts: any[];
    category: {
      name: string;
      label: string;
    };
  };
}

const CategoryPage: React.FC<CategoryPageProps> = props => {
  const { pageContext } = props;
  const { posts, category } = pageContext;
  const relevantPosts = posts.filter(post =>
    post.node.frontmatter.categories.includes(category.name)
  );

  return (
    <Layout>
      <CardBox>
        <div className={style.inside}>
          <h2>Category: {category.label}</h2>
          <hr />
          {relevantPosts.map(post => (
            <div key={post.node.fields.slug} className={style.post}>
              <h3 className={style.title}>
                <Link style={{ boxShadow: "none" }} to={post.node.fields.slug}>
                  {post.node.frontmatter.title || post.node.fields.slug}
                </Link>
              </h3>
              <small>{post.node.frontmatter.date}</small>
              <p dangerouslySetInnerHTML={{ __html: post.node.excerpt }} />
            </div>
          ))}
          {relevantPosts.length === 0 && (
            <h2 style={{ color: "#aaa" }}>Yups...Nothing in here</h2>
          )}
        </div>
      </CardBox>
    </Layout>
  );
};

export default CategoryPage;
